/**
 * @file Fichier userContext.js
 * @description Fichier contenant le contexte utilisateur.
 */
import React, { useState, useEffect, createContext } from "react";
import {
    signInWithEmailAndPassword,
    createUserWithEmailAndPassword,
    onAuthStateChanged,
    sendPasswordResetEmail,
    updatePassword
} from "firebase/auth"
import {auth} from "../firebase.config"

/**
 * @typedef {Object} ModalState
 * @property {boolean} signUpModal - tat du modal d'inscription.
 * @property {boolean} signInModal - tat du modal de connexion.
 */

/**
 * @typedef {Object} UserContextValue
 * @property {ModalState} modalState - tat des modals.
 * @property {function} toggleModals - Fonction pour basculer les modals.
 * @property {function} signUp - Fonction pour crer un compte utilisateur.
 * @property {function} signIn - Fonction pour se connecter avec un compte utilisateur.
 * @property {function} changePasswordFirebase - Fonction pour changer le mot de passe d'un utilisateur.
 * @property {function} forgotPassword - Fonction pour rinitialiser le mot de passe d'un utilisateur.
 * @property {Object} currentUser - Utilisateur actuellement connect.
 */

export const UserContext = createContext();

/**
 * Composant du contexte utilisateur.
 * @param {Object} props - Proprits passes au composant.
 * @returns {JSX.Element} Composant du contexte utilisateur.
 */
export function UserContextProvider(props) {

    /**
     * Fonction pour crer un compte utilisateur.
     * @param {string} email - Adresse e-mail de l'utilisateur.
     * @param {string} pwd - Mot de passe de l'utilisateur.
     * @returns {Promise} Promesse rsolue une fois le compte utilisateur cr.
     */
    const signUp = (email, pwd) => createUserWithEmailAndPassword
        (auth, email, pwd)
    /**
     * Fonction pour se connecter avec un compte utilisateur.
     * @param {string} email - Adresse e-mail de l'utilisateur.
     * @param {string} pwd - Mot de passe de l'utilisateur.
     * @returns {Promise} Promesse rsolue une fois la connexion tablie.
     */
    const signIn = (email, pwd) => signInWithEmailAndPassword
        (auth, email, pwd)
        /**
         * Fonction pour rinitialiser le mot de passe d'un utilisateur.
         * @param {string} email - Adresse e-mail de l'utilisateur.
         * @returns {Promise} Promesse rsolue une fois l'e-mail de rinitialisation envoy.
         */
    const forgotPassword = (email) => sendPasswordResetEmail
        (auth, email, {
            url: process.env.REACT_APP_RESET_PASSWORD_URL,
        })
    /**
     * Fonction pour changer le mot de passe d'un utilisateur.
     * @param {Object} user - Utilisateur dont le mot de passe doit tre modifi.
     * @param {string} newPassword - Nouveau mot de passe de l'utilisateur.
     * @returns {Promise} Promesse rsolue une fois le mot de passe modifi.
     */
    const changePasswordFirebase = (user, newPassword) => updatePassword
        (user, newPassword)

    const [currentUser, setCurrentUser] = useState();
    const [loadingData, setLoadingData] = useState(true);

    useEffect(()=> {
        const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
            setCurrentUser(currentUser)
            setLoadingData(false)
        })
        return unsubscribe;
    }, [])

  /**
   * tat des modals.
   * @type {ModalState}
   */
    const [modalState, setModalState] = useState({
        signUpModal: true,
        signInModal: true
    });

    
  /**
   * Fonction pour basculer les modals.
   * @param {string} modal - Nom du modal  basculer ("signIn", "signUp", "close").
   */
    const toggleModals = modal => {
        if(modal === "signIn") {
            setModalState ({
                signUpModal: true,
                signInModal: false
            });
        }
        if(modal === "signUp") {
            setModalState ({
                signUpModal: true,
                signInModal: false
            });
        }
        if(modal === "close") {
            setModalState ({
                signUpModal: true,
                signInModal: true
            });
        }     
    }
    return (
        <UserContext.Provider value={{modalState, toggleModals, signUp, signIn, changePasswordFirebase, forgotPassword, currentUser}}>
            {!loadingData && props.children}
        </UserContext.Provider>
    );
};